import React, { useEffect, useState } from 'react';
import SectionTitle from '../../SectionTitle';
import CardItem from '../../CardItem/index';
import Carousel from 'react-elastic-carousel';

const saleList = [
      { id: 1, name: 'Liu trnh ngn nga lo ha da, dng da', oldPrice: 880000, salePrice: 780000 },
      { id: 2, name: 'Massage Thi Lan, gim au xng khp', oldPrice: 360000, salePrice: 300000 },
      { id: 3, name: 'Chm sc da mt bng t st t nhin', oldPrice: 3900000, salePrice: 290000 },
      { id: 4, name: 'Liu php lng vai', oldPrice: 253000, salePrice: 230000 },
      { id: 5, name: 'Liu trnh ngn nga lo ha da, dng da', oldPrice: 980000, salePrice: 780000 },
      { id: 6, name: 'Liu trnh ngn nga lo ha da, dng da', oldPrice: 1080000, salePrice: 780000 },
]
interface SaleList {
      id: number,
      name: string,
      oldPrice: number,
      salePrice: number
}
const cardStyle={
      width:'272px'
}
const buttons = [{ id: 1, text: 'Sp ht hn' }, { id: 2, text: 'Gim nhiu' }]
function DetailSaleList(props:any) {
      const { merDetail } = props;
      const title = `u i ca "${merDetail.name}"`;
      const [activeBtn, setActiveBtn] = useState();
      const [productSort, setProductSort] = useState<SaleList[]>([]);
      const [sort, setSort] = useState({
            _sortPrice: saleList
      })
      useEffect(()=>{
            async function handleSort(){
                  setProductSort(saleList)
            }
            handleSort();
      },[sort, productSort])
      const sortPrice=()=>{
            const sortAsc = saleList.sort((a,b)=> (b.oldPrice / b.salePrice) - (a.oldPrice / a.salePrice))
            setSort({
                  ...sort,
                  _sortPrice: sortAsc
            })
      }
      const sortClick = (item: any) => {
            setActiveBtn(item);
            if (item.id === 1) {
                  
            } else if (item.id === 2) {
                  sortPrice()
            }
      }
      return (
            <>
                  <div className="flex-row-sp mer-sale-head">
                        <SectionTitle
                              title={title}
                        />
                        <div className="flex-row mer-sale-head__sort">
                              Sp xp theo:
                              {
                                    buttons.map((item, index) => (
                                          <button
                                                style={activeBtn === item ?
                                                      { backgroundColor: 'var(--purple)', color: 'var(--bg-gray)' }
                                                      :
                                                      {}}
                                                onClick={() => sortClick(item)}
                                                key={index}
                                          >{item.text}</button>
                                    ))
                              }
                        </div>
                  </div>
                  <div className="mer-sale-list">
                        <Carousel
                              children={productSort.map(item => <CardItem key={item.id} style={cardStyle} detail={item} />)}
                              isRTL={false}
                              itemsToShow={4}
                              showArrows={false}
                        />
                         
                  </div>
            </>
      );
}

export default DetailSaleList;